<template>
    <b-container class="navigate-children-container">
        <b-row align-h="center">
            <b-col cols="12" class="border-radius6 padding-sm text-center">

                <b-spinner
                        v-if="loading"
                        variant="info"
                        label="Spinning"
                        style="width: 3rem;height: 3rem;">
                </b-spinner>

                <!-- 文章主体 -->
                <read-card
                        v-if="!loading"
                        v-bind:fullArticle="fullArticle"
                        class="container-item margin-tb-sm border-top-groove border-radius6"
                        style="width: 100%"
                ></read-card>

                <!-- TODO:  微信号、公众号 -->

                <!-- 评论 -->

                <!-- 推荐阅读 -->
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    import ReadCard from "@/components/components/article/ReadCard.vue";

    export default {
        components: {
            ReadCard
        },
        props() {
            article: {
            }
        },
        data() {
            return {
                loading: true,
                fullArticle: {}
            };
        },
        created() {
            this.loadArticle(this.$route.params.id);
        },
        methods: {

            //加载具体文章
            loadArticle(articleId) {
                this.$http({
                    url: this.$http.baseUrl(`/article/info/${articleId}`),
                    method: "GET"
                }).then(({data}) => {
                    if (data && data.code === 0) {
                        !data.article ? (this.$router.push({name: 'index'})) :
                            this.fullArticle = {
                                id: data.article.id,
                                title: data.article.title,
                                content: data.article.content,
                                iconImg: data.article.iconImg,
                                publishTime: data.article.publishTime,
                                authorIcon: data.article.authorIcon,
                                author: data.article.author,
                                authorId: data.article.authorId,
                                watch: data.article.watch,
                                comment: data.article.comment, //评论数
                                collect: data.article.collect, //收藏状态
                                fromLable: data.article.fromLable, //原创、转载
                                articleLable: data.article.articleLable//文章标签：VUE JAVA SpringBoot ...
                            }
                        this.$nextTick(() => {
                            this.loading = false
                        })
                    } else {
                        return Promise.reject(data.msg)
                    }
                }).catch(msg => {
                    this.$bvToast.toast(msg.toString(), {
                        title: '错误',
                        toaster: 'b-toaster-top-center',
                        autoHideDelay: 5000
                    })
                })
            }
        }
    };
</script>

<style scoped>
    .container-item {
        float: left;
        max-width: 100%;
        box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.07);
    }


</style>