<template>
    <div class="read-card padding-lg" style="margin-bottom: 160px;">
        <!-- 文章头部 -->
        <div class="head">
            <h1>{{fullArticle.title}}</h1>
            <div class="detail">
                <div>发布时间:{{fullArticle.publishTime}}</div>
                <b-media class="small-font" style="padding-top:8px;">
                    <b-img
                            :src="fullArticle.authorIcon"
                            class="border-radius3 justify-content"
                            blank-color="#ccc"
                            width="20"
                    />
                    {{fullArticle.author}}
                    <b-div>
                        <b-img :src="$store.state.WebDefaultModule.defaultWatchIcon" blank-color="#ccc" width="20"/>
                        {{fullArticle.watch}}
                    </b-div>
                    <b-div>
                        <b-img :src="$store.state.WebDefaultModule.defaulCollectIcon" blank-color="#ccc" width="20"/>
                        {{fullArticle.collect}}
                    </b-div>
                    <b-div>
                        <b-img :src="$store.state.WebDefaultModule.defaulCommentIcon" blank-color="#ccc" width="20"/>
                        {{fullArticle.comment}}
                    </b-div>

                    <b-div style="float:right;">
                        <!-- time -->
                        <b-div>
                            <b-img :src="$store.state.WebDefaultModule.defaulPublishTimeIcon" blank-color="#ccc"
                                   width="20"/>
                            {{fullArticle.publishTime}}
                        </b-div>
                        <b-div v-for="(item,index) in lable" :key="index">
                            <!-- article-lable -->
                            <b-article-lable class="article-lable">{{fullArticle.item}}</b-article-lable>
                        </b-div>
                        <b-div>
                            <!-- article-from -->
                            <b-article-lable class="article-from">{{fullArticle.fromLable}}</b-article-lable>
                        </b-div>
                    </b-div>
                </b-media>
            </div>
        </div>

        <!--<div  v-html=""></div>-->

        <quill-editor
                ref="quillEditor"
                v-model="fullArticle.content"
                disabled="disabled"
                class="read-quill-editor border-radius6 container-min-height padding-tb-lg"
                style="height: 100%;width: 100%;font-size: 9em !important;"
                :options="editorOption"
        >
        </quill-editor>
    </div>
</template>

<script>
    import {quillEditor} from "vue-quill-editor";
    // import "quill/dist/quill.core.css";
    // import "quill/dist/quill.snow.css";
    // import "quill/dist/quill.bubble.css";
    // import "highlight.js/styles/an-old-hope.css";
    import hljs from "highlight.js/lib";

    export default {
        components: {
            quillEditor
        },
        props: {
            fullArticle: {}
        },

        data() {
            return {
                editorOption: {
                    placeholder: "加载中...",
                    modules: {
                        "toolbar": false,
                        syntax: {
                            highlight: text => hljs.highlightAuto(text).value
                        }
                    }
                }
            }
        }
    };
</script>

<style scoped>
    .read-card {
        background: rgba(255, 255, 255, 0.8);
    }
    .head{
        text-align:left !important;
    }
</style>
<style>
    .read-quill-editor .ql-container {
        border: none !important;
        border-radius: 6px !important;
        background: rgba(255, 255, 255, 0) !important;
    }

    .read-quill-editor .ql-editor {
        position: inherit;
        width: 100%;
        overflow-y: visible;
        background: rgba(255, 255, 255, 0) !important;
        font-size: 1.2em;
    }

    /*.read-quill-editor .ql-formats {*/
        /*max-width: 65% !important;*/
    /*}*/
</style>


